/**
 * Created by caiyu on 2017/1/12.
 * 自定义过滤器
 */
const _filters = {}

_filters.fmtUserIco = (_imgsrc, _path) => {
  let ico = _imgsrc.replace(/_small/g, '')
  return _path + ico
}

export default (Vue) => {
  Object.keys(_filters).forEach(key => {
    Vue.filter(key, _filters[key])
  })
}



