<template>
  <div class="red_user_regsiter">
    <box gap="30px 20px 10px 20px">
      <div class="red_my_user_logo vux-center">
        <img src="../assets/user.jpg" alt="logo">
      </div>
      <group>
        <x-input :placeholder="$t('register.username')" title="" type="text" :min="6" :max="16"></x-input>
        <x-input :placeholder="$t('register.email')" title="" type="text" is-type="email"></x-input>
        <x-input :placeholder="$t('register.password')" title="" type="password" :min="6" :max="16"></x-input>
        <x-input :placeholder="$t('register.rePassword')" title="" type="password" :min="6" :max="16"></x-input>
        <selector v-if="isChinese" :placeholder="$t('register.sex')" :options="lists" title="" @on-change=""></selector>
        <selector v-else :placeholder="$t('register.sex')" :options="listEn" title="" @on-change=""></selector>
      </group>
    </box>
    <box gap="30px 20px">
      <x-button class="btn-blueer">{{ $t('register.register') }}</x-button>
    </box>
  </div>
</template>
<script>
  import { Group, Box, XInput, Selector, XButton } from 'vux/src/components'
  import store from '../vuex/store'

  export default {
    data () {
      return {
        list: [{key: '0', value: '男'}, {key: '1', value: '女'}],
        listEn: [{key: '0', value: 'Male'}, {key: '1', value: 'Female'}]
      }
    },
    components: {
      Group,
      Box,
      XInput,
      Selector,
      XButton
    },
    store,
    computed: {
      isChinese () {
        let history = window.sessionStorage
        if (history.getItem('locale') === 'ch' || history.getItem('locale') === 'ft') {
          return true
        } else {
          return false
        }
      }
    }
  }
</script>
<style>
  .red_user_regsiter .red_my_user_logo {
    width: 8em;
    height: 8em;
  }
  .red_user_regsiter .weui_cell:before {
    left: 0;
  }
</style>



