import {
  UPDATE_DIRECTION,
  UPDATE_LOADING,
  UPDATE_GOTO,
  UPDATE_MY,
  UPDATE_LANGUAGE,
  UPDATE_TITLE
} from '../mutations-type'

let state = {
  isLoading: false,
  direction: 'forward',
  isMy: false,
  server: 'http://172.16.129.209:800/mobile.php',
  imgsrc: 'http://172.16.129.209:800/',
  lang: 'en',
  title: {}
}

const mutations = {
  [UPDATE_LOADING] (state, status) {
    state.isLoading = status
  },
  [UPDATE_DIRECTION] (state, direction) {
    state.direction = direction
  },
  [UPDATE_MY] (state, my) {
    state.isMy = my
  },
  [UPDATE_GOTO] (state, goto) {
    state.isGoto = goto
  },
  [UPDATE_LANGUAGE] (state, lang) {
    state.lang = lang
  },
  [UPDATE_TITLE] (state, title) {
    state.title = title
  }
}

export default {
  state,
  mutations
}



