<template>
  <div class="red-gift-body">
    <div class="red-gift-img" :style="style">
      <div class="red-gift-inner">
        <img src="../assets/user.jpg" :src="imgsrc">
        <a href="javascript:void(0)" @click="showActionSheet = true">
          <span class="redfont icon-font icon-sandianshu"></span>
        </a>
      </div>
    </div>
    <actionsheet
      :show.sync="showActionSheet"
      :menus="{'select':$t('gift.selectFri'),'give':$t('gift.give')}"
      @on-click-menu="click"
      show-cancel
      :cancel-text="$t('gift.cancel')"></actionsheet>
  </div>
</template>
<script>
  import { XImg, Actionsheet } from 'vux/src/components'
  import { getUserInfo, getLanguage, baseServerPath, baseImgSrc } from '../vuex/getters'

  export default {
    components: {
      XImg,
      Actionsheet
    },
    vuex: {
      getters: {
        getUserInfo,
        getLanguage,
        baseServerPath,
        baseImgSrc
      }
    },
    methods: {
      click (key) {
        console.log(key)
      },
      getuserico () {
        this.$http.jsonp(this.baseServerPath, {
          params: {
            act: 'getuserico',
            lang: this.getLanguage,
            isMobile: true,
            userid: this.userid
          },
          callback: '_callback'
        }).then((response) => {
          let res = JSON.parse(response.data)
          if (res.success) {
            this.imgsrc = res.icon
          }
        }, (response) => {

        })
      },
      getfriends () {
        this.$http.jsonp(this.baseServerPath, {
          params: {
            act: 'getfriend',
            lang: this.getLanguage,
            isMobile: true,
            userid: this.getUserInfo['user_id']
          },
          callback: '_callback'
        }).then((response) => {
          // 成功回调
          let res = JSON.parse(response.data)
          if (res.success) {
            let temp = []
            this.friends = res.msg
            this.friends.forEach(function (item, index) {
              temp.push(item['pals_name'])
            })
            this.friendList.push(temp)
          } else {
            console.log(res.msg)
          }
        }, (response) => {
          // 失败回调
          console.log(response.data)
        })
      }
    },
    data () {
      return {
        showActionSheet: false,
        userid: '',
        friendList: [],
        imgsrc: this.baseImgSrc + 'skin/default/jiaoyou/images/d_ico_1.gif',
        style: 'background-image:url(' + this.baseImgSrc + 'skin/default/jiaoyou/images/d_ico_1.gif' + ')'
      }
    },
    created () {
      this.userid = this.$route.params.userId
      // 如果是系统默认
      if (this.userid !== 'default') {
        // 等下处理
      } else {
        this.userid = this.getUserInfo['user_id']
        this.imgsrc = this.baseImgSrc + this.getUserInfo['user_ico'].replace(/_small/g, '')
        this.style = 'background-image:url(' + this.imgsrc + ')'
      }
    }
  }
</script>
<style>
  .red-gift-body {
    margin-top: -50px;
  }
  .red-box-fix .red-gift-body {
    margin-top: -70px;
  }
  .red-gift-img {
    background-repeat: no-repeat;
    background-position: center center;
    height: 240px;
    margin-top: -5px;
    background-size: cover;
    position: relative;
  }
  .red-gift-img > .red-gift-inner {
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.75);
    backdrop-filter: blur(6px);
    text-align: center;
  }
  .red-gift-inner > img {
    display: inline-block;
    width: 150px;
    height: 150px;
    margin-top: 45px;
    border-radius: 100%;
    box-shadow: 0 0 20px rgba(0,0,0,0.75);
  }
  .red-gift-inner > a {
    position: absolute;
    right: 0;
    top: 10px;
    color: #fff;
    width: 35px;
  }
  .red-box-fix .red-gift-inner > a {
    top: 20px;
  }
</style>



