<template>
  <div class="red_user_login">
    <a v-link="{path:'/lang'}" class="red_user_login_lang">{{ $t("base.language") }}</a>
    <div class="red_my_user_logo vux-center">
      <img src="../assets/user.jpg" alt="logo" v-if="isNone">
      <img src="../assets/user.jpg" :src="userico" alt="logo" v-else>
    </div>
    <box gap="20px 0">
      <group>
        <x-input title="" type="text" :placeholder="$t('login.username')"
                 :min="6" :max="16" :value.sync="username" @change="getusericon(baseServerPath)"></x-input>
        <x-input title="" type="password" :placeholder="$t('login.password')"
                 :min="6" :max="16" :value.sync="password"></x-input>
      </group>
    </box>
    <box gap="30px 10px 5px 10px">
      <x-button class="btn-blueer" @click="login(baseServerPath)">{{ $t("login.login") }}</x-button>
    </box>
    <box gap="0 10px">
      <a v-link="{path:'/regsiter'}" class="red_link_reg">{{ $t("login.newRegister") }}</a>
    </box>
    <alert :show.sync="showToast" :title="$t('register.title')">{{ errorText }}</alert>
    <alert :show.sync="showAlert" :title="$t('register.title')">{{ $t('base.incognitoMode') }}</alert>
  </div>
</template>
<script>
  import { XInput, Box, Group, XButton, Alert } from 'vux/src/components'
  import { baseServerPath, baseImgSrc, getUserInfo, getLanguage } from '../vuex/getters'
  import { setUserInfo } from '../vuex/actions'

  export default {
    components: {
      XInput,
      Box,
      Group,
      XButton,
      Alert
    },
    data () {
      return {
        username: '', // 登录用户名
        password: '', // 登录密码
        showToast: false, // 是否显示错误提示框
        errorText: '', // 错误提示信息
        userico: '', // 用户头像
        isNone: true, // 是否获取头像
        showAlert: false
      }
    },
    vuex: {
      getters: {
        baseServerPath,
        baseImgSrc,
        getUserInfo,
        getLanguage
      },
      actions: {
        setUserInfo
      }
    },
    methods: {
      // 用户登录
      login (server) {
        let self = this
        let storage = window.localStorage
        this.$http.jsonp(server, {
          params: {act: 'login', username: self.username, password: self.password, lang: self.getLanguage, 'isMobile': true},
          callback: '_callback'
        }).then((response) => {
          // 成功回调
          let res = JSON.parse(response.data)
          if (res.success) {
            // 成功回调，同步数据
            self.setUserInfo(res.msg)
            storage.setItem('userinfo', JSON.stringify(res.msg))
            storage.setItem('mypals', JSON.stringify(res.mypals))
            window.router.go({
              path: '/',
              replace: false
            })
          } else {
            self.errorText = res.msg
            self.showToast = true
          }
        }, (response) => {
          // 失败回调
          console.log(response)
        })
      },
      // 获取用户头像
      getusericon (server) {
        let self = this
        this.$http.jsonp(server, {
          params: {act: 'login', username: self.username, icon: true, lang: self.getLanguage, 'isMobile': true},
          callback: '_callback'
        }).then((response) => {
          // 成功回调
          let res = JSON.parse(response.data)
          if (res.icon) {
            // 显示高清头像
            if (res.icon.indexOf('_small') !== -1) {
              res.icon = res.icon.replace(/_small/g, '')
            }
            self.isNone = false
            self.userico = self.baseImgSrc + res.icon
          }
        }, (response) => {
          // 失败回调
        })
      }
    },
    created () {
      // 判断浏览器是否处于无痕（隐私）模式
      try {
        window.localStorage.incognitoMode = 'incognitoMode'
      } catch (ex) {
        this.showAlert = true
      }
    }
  }
</script>
<style>
  .red_user_login {
    position: absolute;
    background: url(../assets/site_bg.jpg) no-repeat;
    height: 100%;
    top: 0;
    bottom: 0;
    z-index: 100;
  }
  .red_user_login .red_my_user_logo {
    width: 8.5em;
    height: 8.5em;
  }
  .red_user_login .red_my_user_logo {
    margin-top: 60px;
  }
  .red_user_login .weui_cell:before {
    left: 0;
  }
  .red_user_login .weui_cell input {
    text-align: center;
  }
  .red_link_reg {
    color: #20a0ff;
    font-size: 14px;
  }
  .red_user_login .weui_btn_dialog.primary {
    color: #20a0ff;
  }
  .red_user_login_lang {
    position: absolute;
    right: 15px;
    top: 20px;
    color: #20a0ff;
  }
</style>



